@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    border: 0;
    font-family: 'Roboto', sans-serif;
}

html, body, #root, #containerLogin {
    height: 100%;
    width: 100%;
    display: flex;
    background-color: var(--primary-background-color);
    flex: 1 1;
}


#containerLogin {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
#box-login {
    width: 500px;
    height: 50%;
    background-color: rgba(218, 217, 236, 0);
    border-radius: 10px;
    align-items: center;
    justify-content: center;
}
header {
    width: 100%;
    height: 45px;
    display: flex;
    align-items: center;
    padding-left: 20px;
    border-radius: 5px 5px 0 0;
    color: rgb(6, 2, 66);
}
h2 {
    color: #fff;
    font-size: 25px;
    text-align: center;
    border-bottom: 1px solid #000;
}
main {
    width: 100%;
    height: 200px;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
}
input {
    height: 40px;
    width: 90%;
    margin-top: 20px;
    border-radius: 0.3em;  
    border: 0.5px solid var(--inputs-border-color); 
}
#area-buttons {
    width: 100%;
    height: 70px;
    justify-content: space-between;
    align-items: center;
    display: flex;
}
button {
    width: 90%;
    height: 40px;
    border-radius: 5px;
    font-size: 15px;
    background-color: var(--button-background-color) ;
    color: #fff;
    cursor: pointer;
    margin-left: 24px;
}

a {
    text-decoration: none;
}

span {
    color: #fff;
    font-size: small;
}

@media only screen and (max-width: 599px){
    #box-login {
        width: 100%;
    }
    span{
        display: none;
    }
}


* {
  padding: 0;
  margin: 0;
  box-sizing: 0;
  font-family: 'Roboto', sans-serif;
}

html, body, #root, #container  {
  height:100%;
  width: 100%;
  overflow-x: visible;

}

.blue {
  background: rgb(9, 87, 160);
}
.darkblue {
  background: var(--primary-background-color);
}

#container {
  display: flex;
  flex-direction: row;
  background-color: #fff;
  overflow: auto;
  
}
.floors {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  padding-bottom: 13px;
  width: 100%;
  font-size: 20px;
  color: #fff;
  font-weight: 900;
  height: 45px;
  text-align: center;
  line-height: 45px;
}

.imageCostumer {
  width: 130px;
  height: 60px;
  margin-bottom: 5px;
  image-resolution: 300dpi;
}
.numberCostumer {
  color: rgb(9, 0, 78);
  font-weight: 600;
}

.containerRoom {
  display: flex;
  flex-direction: column;
  margin: 0px 0px 5px 5px;
}


@media only screen and (max-width: 599px){
  .floors {
    width: 411px;
  }
  .imageCostumer {
    width: 60%;
  }
  .numberRoom {
    width: 30%;
  }
  .containerWeather {
      display: none;
  }
}

#containerAdministration {
    height: 100%;
    display: flex;
    flex: 1 1;
    justify-content: space-evenly;
    align-items: center;
}
#containerNavigation {
    width: 100%;
    max-width: 230px;
    height: 480px;
}

#containerNavigation ul {
    width: 100%;
    background: #fff;
    border-radius: 0.2em;
}
#containerNavigation ul li p {
    font-weight: bold;
}
#containerNavigation ul li:nth-child(7) {
    color: rgb(153, 11, 11);
}
#containerNavigation ul li:hover {
    cursor: pointer;
    background: rgb(219, 219, 219);
}

#containerNavigation ul li {
    padding-left: 10px;
    height: 50px;
    text-align: start;
    font-family: 'Roboto',sans-serif;
    font-size: small;
    font-weight: bold;
    border-bottom: 1px solid rgb(182, 182, 182);
    color: #000;
    display: flex;
    align-items: center;
}

#containerFunctional {
    width: 60%;
    max-width: 630px;
    height: 480px;
}

#continerTopToMobile, #list-menu {
    display: none;
}
.subMenu ul{ 
    display: none !important;
    position: absolute;
    width: 200px !important;
    margin-left: 220px;
    margin-top: 25px;
    background: #fff;
    flex-direction: column;
}
.subMenu {
    text-align: left;
    display: flex;
    justify-content: center;
    border-radius: none !important;
    padding-left: 10px;
    font-size: small    ;
    flex-direction: column;
    font-family: 'Roboto',sans-serif;
    font-weight: bolder;
    height: 50px;
}
.subMenu:hover > ul {
    display: flex !important;
}
@media only screen  and (max-width: 599px){
    #continerTopToMobile {
        display: flex;
        width: 100%;
        height: 65px;
        background: #fff;
        position: absolute;
        flex-direction: row;
        align-items: center;
        background: rgb(253, 253, 253);
    }
    #contianerCreateUser {
        border: none !important;
    }
    #buttonTopMenu {
        color: #fff;
        width: 40px;
        height: 30px;
        margin-top: 10px;
        margin-left: 10px;
        cursor: pointer;
    }
    #containerFunctional {
        width: 100%;
    }
    #containerNavigation {
        display: none;
    }
    #list-menu {
        position: fixed;
        background: rgb(255, 255, 255);
        display: block;
        top: 60px;
        width: 300px;
        height: 100%;
        left: -300px;
        transition: all 0.25s;
        -webkit-transition: all 0.25s;
    }
    #list-menu li {
        width: 100%;
        display: block;
        font-size: medium;
        text-align: left;
        color: rgb(1, 1, 46);
        font-weight: bold;
        font-family: 'Roboto',sans-serif;
        margin-top: 8px;
        line-height: 40px;
        padding-left: 10px;
        height: 40px;
        cursor: pointer;
    }
    #list-menu li:hover {
        background: rgb(151, 151, 151);
    }
}

#contianerCreateUser {
    flex: 1 1;
    width: 100%;
    height: 100%;
    display: flex;
    align-items:center ;
    flex-direction: column;
    padding-top: 40px;  
