#contianerAddRoom {
    flex: 1 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding-top: 40px;
    padding-left: 20px;
    border-left: 0.1px solid #000;
}

#contianerAddRoom h2 { 
    text-align: start;
}

input[type="file"]::-webkit-file-upload-button{
    visibility: hidden;
    opacity: 0;
}

input[type="file"]::before {
    content: 'Enviar imagem';
    display: inline-block;
    height: 80%;
    width: 120px;
    background: #e3e3e3;
    border-radius: 0.5em;
    padding: 5px 10px 0px 50px;
    z-index: 1;
    white-space: nowrap;
    -webkit-user-select: none;
    cursor: pointer;
    text-shadow: 1px 1px #fff;
    font-weight: 900;
    line-height: 30px;
    font-size: 10pt;
}

input[type="file"]{
    border: blue;
}

#contianerAddRoom div {
    width: 100%;
    height: 150px;
    display: flex;
    justify-content: left;
    align-items: flex-end;
}

#contianerAddRoom div button {
