.containerActionAlert {
    position: absolute;
    width: 200px;
    height: 50px;
    right: 5px;
    background: var(--actionalert-background-color);
    -webkit-animation: showalert 3s;
            animation: showalert 3s;
    border-radius: 0.3em;
    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.containerActionAlert p {
    color: aliceblue;
}

@-webkit-keyframes showalert {
    from {
        top: 0px;
        opacity: 0;
    }
    to {
        top: 50px;
        opacity: 1;

    }
}

@keyframes showalert {
    from {
        top: 0px;
        opacity: 0;
    }
    to {
        top: 50px;
        opacity: 1;

    }
}
:root{
    --primary-background-color: #000e55;
    --actionalert-background-color: rgb(0, 87, 3);
    --button-background-color: #079fd6;
    --inputs-border-color: rgb(144, 144, 144);
}


